import './Userstyle.css';
import { useState } from "react";
import { Link } from "react-router-dom";
import Logo from "../assets/foodiKo1.png";
import HomeIcon from "@mui/icons-material/Home";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";

const Resetpwd = () => {

  const [typerpwd1, setTyperpwd1] = useState("password");
  const [iconrpwd1, setIconrpwd1] = useState(<VisibilityOffIcon />);

  const handleTogglerpwd1 = () => {
    if (typerpwd1 === "password") {
      setIconrpwd1(<VisibilityIcon />);
      setTyperpwd1("text");
    } else {
      setIconrpwd1(<VisibilityOffIcon />);
      setTyperpwd1("password");
    }
  };

  const [typerpwd2, setTyperpwd2] = useState("password");
  const [iconrpwd2, setIconrpwd2] = useState(<VisibilityOffIcon />);

  const handleTogglerpwd2 = () => {
    if (typerpwd2 === "password") {
      setIconrpwd2(<VisibilityIcon />);
      setTyperpwd2("text");
    } else {
      setIconrpwd2(<VisibilityOffIcon />);
      setTyperpwd2("password");
    }
  };

  return (
    <div className="user-container">
      <div className="n-nav" >
        <div className="n-foodikologo">
          <img src={Logo} alt="foodiko" />
        </div>
        <Link className="n-link" to="/">
          <button>
            <span>
              <HomeIcon />
            </span>
            <span>Home</span>
          </button>
        </Link>
      </div >
      <div className="user-cont">
        <form className="user-form">
          <p className="u-name">Reset Password</p>
          <div className="u-input">
            <span>
              <label htmlFor="new_password">
                New Password
              </label>
            </span>
            <span className="u-icon u-eyeicon" onClick={handleTogglerpwd1}>
              {iconrpwd1}
            </span>
            <br></br>
            <input type={typerpwd1} id="new_password" required></input>
            <div className="screen1"></div>
          </div>
          <div className="u-input">
            <span>
              <label htmlFor="confirm_password">
                Confirm Password
              </label>
            </span>
            <span className="u-icon u-eyeicon" onClick={handleTogglerpwd2}>
              {iconrpwd2}
            </span>
            <br></br>
            <input type={typerpwd2} id="confirm_password" required></input>
            <div className="screen2"></div>
          </div>
          <div className="u-btn1">
            <Link to="/login">
              <button type="submit" className="u-btn">Confirm</button>
            </Link>
          </div>
        </form>
      </div>
    </div>
  )
}

export default Resetpwd;