import './Userstyle.css';
import { useState } from "react";
import { Link } from "react-router-dom";
import Logo from "../assets/foodiKo1.png";
import HomeIcon from "@mui/icons-material/Home";
import PersonIcon from "@mui/icons-material/Person";
import EmailIcon from "@mui/icons-material/Email";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";

const Signup = () => {

  const [typesgn1, setTypesgn1] = useState("password");
  const [iconsgn1, setIconsgn1] = useState(<VisibilityOffIcon />);

  const handleTogglesgn1 = () => {
    if (typesgn1 === "password") {
      setIconsgn1(<VisibilityIcon />);
      setTypesgn1("text");
    } else {
      setIconsgn1(<VisibilityOffIcon />);
      setTypesgn1("password");
    }
  };

  const [typesgn2, setTypesgn2] = useState("password");
  const [iconsgn2, setIconsgn2] = useState(<VisibilityOffIcon />);

  const handleTogglesgn2 = () => {
    if (typesgn2 === "password") {
      setIconsgn2(<VisibilityIcon />);
      setTypesgn2("text");
    } else {
      setIconsgn2(<VisibilityOffIcon />);
      setTypesgn2("password");
    }
  };

  return (
    <div className="user-container">
      <div className="n-nav" >
        <div className="n-foodikologo">
          <img src={Logo} alt="foodiko" />
        </div>
        <Link className="n-link" to="/">
          <button>
            <span>
              <HomeIcon />
            </span>
            <span>Home</span>
          </button>
        </Link>
      </div >
      <div className="user-cont">
        <form className="user-form">
          <p className="u-name">Create Account</p>
          <div className="u-input">
            <span>
              <label htmlFor="username">
                Username
              </label>
            </span>
            <span className="u-icon">
              <PersonIcon />
            </span>
            <br></br>
            <input type="text" id="username" required></input>
            <div className="screen1"></div>
          </div>
          <div className="u-input">
            <span>
              <label htmlFor="email">
                Email
              </label>
            </span>
            <span className="u-icon">
              <EmailIcon />
            </span>
            <br></br>
            <input type="email" id="email" required></input>
            <div className="screen2"></div>
          </div>
          <div className="u-input">
            <span>
              <label htmlFor="password">
                Password
              </label>
            </span>
            <span className="u-icon u-eyeicon" onClick={handleTogglesgn1}>
              {iconsgn1}
            </span>
            <br></br>
            <input type={typesgn1} id="password" required></input>
            <div className="screen3"></div>
          </div>
          <div className="u-input">
            <span>
              <label htmlFor="con_password">
                Confirm Password
              </label>
            </span>
            <span className="u-icon u-eyeicon" onClick={handleTogglesgn2}>
              {iconsgn2}
            </span>
            <br></br>
            <input type={typesgn2} id="con_password" required></input>
            <div className="screen4"></div>
          </div>
          <div className="u-btn1">
            <Link to="/">
              <button type="submit" className="u-btn">Sign up</button>
            </Link>
          </div>
          <div className="u-link1">
            <span className="u-font2">Already have an account? </span>
            <span>
              <Link to="/login" className="u-font1">
                login
              </Link>
            </span>
          </div>
        </form>
      </div >
    </div >
  )
}

export default Signup;