import { useContext } from "react";
import { Cartpage } from "../Context";
import Cartlist from "./Cartlist";
import "./css/Cart.css";
import Navbar from "./Navbar";
import HomeIcon from "@mui/icons-material/Home";
import { Link } from "react-router-dom";

const Cart = () => {
  const { cart1 } = useContext(Cartpage);

  return (
    <>
      <div className="cart-parent">
        <Navbar />
        <div className="cart-cont1">
          <br></br>
          <p>Cart</p>
          <br></br>
          <div className="cart-cont">
            {cart1.map((items, cart1index) => (
              <Cartlist items={items} cart1index={cart1index} key={items.id} />
            ))}
          </div>
        </div>
        <div className="cart-foot">
          <div className="cart-foot1">
            <div>
              <button className="porder" onClick={() => alert("Order has been Placed")}>Place Order</button>
            </div>
            <div className="cart-tot">
              <h5>Total</h5><span> <p> {
                cart1.map(items => items.price * items.quantity).reduce((total, value) => total + value, 0)}</p></span>
            </div>
          </div>
        </div>
      </div>
      <div className="c-home-icon1">
        <div className="c-home-icon">
          <Link to="/">
            <HomeIcon className="c-home" />
          </Link>
        </div>
      </div>
    </>
  );
};

export default Cart;
