import "./css/Footer.css";
import InstagramIcon from "@mui/icons-material/Instagram";
import TwitterIcon from "@mui/icons-material/Twitter";
import GitHubIcon from "@mui/icons-material/GitHub";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import MailOutlineIcon from "@mui/icons-material/MailOutline";

const Footer = () => {
  return (
    <div className="footer-cont1">
      <div className="footer-cont">
        <div className="f-social">
          <a
            href="https://www.instagram.com/dharunkumar_s/"
            className="instagram"
            title="Instagram"
            target="_blank"
          >
            <InstagramIcon className="f-icon " sx={{ fontSize: 32 }} />
          </a>
          <a
            href="https://twitter.com/dharun_kumar_s"
            className="twitter"
            title="Twitter"
            target="_blank"
          >
            <TwitterIcon className="f-icon " sx={{ fontSize: 32 }} />
          </a>
          <a
            href="https://www.linkedin.com/in/dharunkumars/"
            className="linkedin"
            title="LinkedIn"
            target="_blank"
          >
            <LinkedInIcon className="f-icon " sx={{ fontSize: 32 }} />
          </a>
          <a
            href="https://github.com/Dharunkumar-S/"
            className="github"
            title="Github"
            target="_blank"
          >
            <GitHubIcon className="f-icon " sx={{ fontSize: 32 }} />
          </a>
          <a
            href="mailto:dharunkumar1104@gmail.com"
            className="mail"
            title="Mail"
          >
            <MailOutlineIcon className="f-icon " sx={{ fontSize: 32 }} />
          </a>
        </div>
        <div className="copy-txt">Copyright All rights reserved</div>
      </div>
    </div>
  );
};

export default Footer;
