import { Suspense, lazy } from "react";
import Navbar from "./Navbar";
import Footer from "./Footer";
import Loader from "./Loader";
const Home = () => {
  const Itemlist = lazy(() => import("./Itemlist"));
  return (
    <div className="home-container1">
      <Navbar />
      <Suspense fallback={<Loader />}>
        <Itemlist />
        <Footer />
      </Suspense>
    </div>
  );
};

export default Home;
