import { useContext } from "react";
import { Cartpage } from "../Context";
import "./css/Cartlist.css";
import DeleteIcon from "@mui/icons-material/Delete";

const Cartlist = ({ items, cart1index }) => {
  const { cart1, setCart1 } = useContext(Cartpage);

  return (
    <div className="clist-cont">
      <div className="clist-img">
        <img src={items.imgsrc} alt={items.name} loading="lazy"></img>
      </div>
      <div className="clist-body">
        <div className="clist-name">{items.name}</div>
        <div className="clist-price">
          <span></span>
          {items.price}
        </div>
        <div className="clist-count">
          <button className="c-minus" onClick={() => {
            if (items.quantity > 1) {
              const _cart1 = cart1.map((items, index) => {
                return cart1index === index ? { ...items, quantity: items.quantity - 1 } : items
              })
              setCart1(_cart1)
            }
          }}
          >-</button>
          <p>{items.quantity}</p>
          <button className="c-plus" onClick={() => {
            const _cart1 = cart1.map((items, index) => {
              return cart1index === index ? { ...items, quantity: items.quantity + 1 } : items
            })
            setCart1(_cart1)
          }}>+</button>
        </div>
      </div>
      <div className="clist-body1">
        <div title="Remove">
          <DeleteIcon
            className="i-cart1"
            sx={{ fontSize: 30 }}
            onClick={() => {
              setCart1(cart1.filter((c) => c.id !== items.id));
            }}
          />
        </div>
        <div className="clist-tprice">
          <span></span>{items.price * items.quantity}
        </div>
      </div>
    </div>
  );
};

export default Cartlist;
